<template>
  <Row class="w-180 h-88 pr-border radius-8 p-lr-20" x="sb" @click="detailPhoneAreaCode.show=true">
    <span v-if="modelValue" class="font-32 bold">{{ modelValue }}</span>
    <span v-else class="c-info">{{ $t('form.selectpl') }}</span>
    <IconSvg name="icon-invite_select" class="font-20 c-placeholder" />
  </Row>
  <van-popup v-model:show="detailPhoneAreaCode.show" teleport="body" position="bottom">
    <Row x="sb" class="w-100b p-lr-30 h-110" y="c">
      <el-input
        v-model="areaSearch"
        class="area-search"
        :placeholder="$t('user.plsCountryCode')"
      >
        <template #prefix>
          <IconSvg name="icon-invite_search" class="font-28" />
        </template>
      </el-input>
      <Row class="font-24 pointer" @click="detailPhoneAreaCode.show=false">{{ $t('alert.cancel') }}</Row>
    </Row>
    <div class="h-968 area-list-box">
      <Row
        v-for="(e, i) in areaSearchList(areaList)"
        :key="i"
        y="c"
        x="sb"
        class="h-88 p-lr-30 w-100b"
        @click="onAreaSelect(e)"
      >
        <Row class="font-28">
          <img :src="e.icon" onerror="this.src='/image/backup.webp'" class="w-34 h-24 radius-2 m-r-17">
          <span>{{ e.nationalityName }}</span>
        </Row>
        <span class="c-info font-28">{{ e.phoneAreaCode }}</span>
      </Row>
    </div>
  </van-popup>
</template>
<script setup>
let areaList = $ref([])
const emit = defineEmits()
const props = defineProps({
  modelValue: {
    default: ''
  }
})
const areaSearch = $ref('')
const detailPhoneAreaCode = reactive({
  show: false
})
const areaSearchList = () => {
  if (!areaSearch) {
    return areaList
  }
  const searchKeys = ['phoneAreaCode', 'nationalityName']
  return areaList.filter(d => {
    return d[searchKeys[0]].toString().indexOf(areaSearch.toString()) !== -1 || d[searchKeys[1]].toString().indexOf(areaSearch.toString()) !== -1
  })
}

const getAreaList = async () => {
  const data = await userApi.areaList()
  areaList = data
}

const onAreaSelect = (e) => {
  emit('update:modelValue', e.phoneAreaCode)
  detailPhoneAreaCode.show = false
}

onBeforeMount(async () => {
  await getAreaList()
  emit('update:modelValue', areaList[0]?.phoneAreaCode)
})

</script>
