
<template>
  <Column class="p-lr-30 p-t-60 phone-register">
    <el-form
      ref="formRef"
      :model="form"
      :rules="rules"
      class="w-100b error-initial"
    >
      <el-form-item
        key="phone"
        prop="phone"
      >
        <phoneAreaCode v-model="form.phoneAreaCode" />
        <el-input
          v-model="form.phone"
          class="h-88 radius-8 f-1 m-l-20 bold"
          :placeholder="$t(`form.phone`)"
          :maxlength="32"
          @keyup.enter="submit"
          @input="(val)=>form.phone = val.replace(/[^0-9]/g, '')"
        />
      </el-form-item>

      <el-form-item
        key="verificationCode"
        prop="verificationCode"
      >
        <Row class="w-100b position-r">
          <el-input
            v-model="form.verificationCode"
            :maxlength="6"
            :placeholder="$t('form.verifyCode')"
            class="bold"
            @input="(val)=>form.verificationCode = val.replace(/[^0-9]/g, '')"
            @keyup.enter="submit"
          />
          <el-form-item>
            <CodeButton
              class="m-l-20 h-100b h-88 p-r-20"
              :account="form.phone"
              :getVerifyCode="getVerifyCode"
              :beforeClick="beforeClickGetCode"
              type="0"
              :phoneAreaCode="form.phoneAreaCode"
            />
          </el-form-item>
        </Row>
      </el-form-item>

      <el-form-item
        class="m-b-40"
        prop="loginPassword"
      >
        <InputPwd
          v-model="form.loginPassword"
          :placeholder="$t('form.inputPl','form.password')"
          :showPwdStrengt="false"
          class="bold"
          maxlength="32"
          @keyup.enter="submit"
          @blur="() => loginPasswordIsErr = !form.loginPassword"
          @focus="() => $v.el.field(formRef, 'loginPassword')"
        />
      </el-form-item>

      <Row v-if="!cookies.get('inviteCode')" class="m-b-15" @click="isInviteCode=!isInviteCode">{{ $t('user.inviteCodeOptional') }}
        <IconSvg v-if="isInviteCode" name="icon-invite_select" class="font-16 m-l-4 c-info" />
        <IconSvg v-else name="icon-invite_select" class="font-16 m-l-4 c-info rotate180" />
      </Row>
      <el-form-item v-if="isInviteCode">
        <el-input v-model="form.inviteCode" class="bold" />
      </el-form-item>
    </el-form>

    <el-button type="primary" class="w-100b font-w-400 font-32" :loading="loading" @click="submit">{{ $t('form.nowRegister') }}</el-button>
    <Row class="w-100b m-b-10 label font-24 m-t-13" y="c">
      <span v-if="lang()==='en'" class="c-info m-r-10">{{ $t("form.readAndAccept") }}</span>
      <span v-else class="c-info">{{ $t("form.readAndAccept") }}</span>
      <span class="c-primary break-word" @click="toUserAgreement">{{
        $t("form.userAgreement")
      }}</span>
    </Row>
  </Column>

</template>
<script setup>

const userStore = useUserStore()
const route = useRoute()
const formRef = $ref(null)
const isInviteCode = $ref(false)
let loading = $ref(false)
let loginPasswordIsErr = $ref(false)
const form = reactive({
  phoneAreaCode: '',
  phone: sessionStorage.phone ?? '',
  loginPassword: sessionStorage.loginPassword ?? '',
  verificationCode: sessionStorage.verificationCode ?? '',
  inviteCode: route.query.inviteCode ?? '',
  channelId: '',
})

const rules = {
  phone: [
    { required: true, message: $t('form.inputPl', 'form.phone'), trigger: 'blur' },
    // { validator: $v.el.check($v.re.phone, { errorText: () => $t('form.phoneError') }), trigger: 'blur' },
    { validator: $v.el.check($v.re.lan5, { errorText: () => $t('form.plsRightPhone') }), trigger: 'blur' },
  ],

  verificationCode: [
    { required: true, message: $t('user.plsCode'), trigger: 'blur' },
  ],
  loginPassword: [
    {
      required: true,
      message: $t('user.authpass'),
      trigger: 'blur',
    },
    {
      validator: (rule, value, callback) => {
        if ($v.pwdStrengt.verifyPass(form.loginPassword)) {
          callback()
        } else {
          callback($t('user.authpass'))
        }
      },
      trigger: ['blur'],
    },
  ]
}

const beforeClickGetCode = async () => {
  await $v.el.field(formRef, 'phone')
  return true
}

const getVerifyCode = async (obj = {}) => {
  const body = {
    type: 1,
    smsType: 0,
    phoneOrEmail: 1,
    ...obj
  }
  body.phone = form.phone
  body.phoneAreaCode = form.phoneAreaCode

  const data = await userApi.getVerifyCode(body)
  return data
}
const toUserAgreement = () => {
  // toRouter(`/help?name=userAgreement&${location.search.replace('?', '')}`)
  const url = `/help?name=userAgreement&${location.search.replace('?', '')}`
  window.open(url, '_blank')
}

import phoneAreaCode from './phoneAreaCode.vue'
import useLogin from '@/view/user/RegisterUpLand/hooks/useLogin.js'
const { login } = useLogin()

const submit = async () => {
  try {
    await $v.el.field(formRef)
    loading = true
    const body = {
      ...form,
      registerWay: 1,
      loginPassword: encrypt.md5(form.loginPassword),
    }

    await userApi.registered(body)
    const loginBody = {
      phoneOrEmail: body.phone,
      phoneAreaCode: body.phoneAreaCode,
      loginPassword: body.loginPassword,
    }
    await login(loginBody)
    toRouter(`/user/registerSuccess?${location.search.replace('?', '')}`)
  } catch (error) {
    loginPasswordIsErr = !form.loginPassword
  } finally {
    loading = false
  }
}

const getCookieCode = (key) => {
  const code = $ref(cookies.get(key))
  if (!code) {
    if (route.query[key]) {
      cookies.set(key, route.query[key], 1 * 60 * 60 * 24 * 7)
      form[key] = route.query[key]
    } else {
    }
  } else {
    form[key] = code
  }
}

onBeforeMount(() => {
  // showToast('提示内容')
  getCookieCode('inviteCode')
  getCookieCode('appId')
})

</script>
